import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AuthService } from '../common/service/authentication/auth.service';
import { CriptoService } from '../common/service/encryption/cripto.service';
import { WorkflowService } from '../common/service/workflow/workflow.service';
import { SendInformationService } from '../common/service/SendInformation/send-information.service';
import { TranslateService } from '@ngx-translate/core';
import { STATE_ERROR, PRODUCTO, INITIAL } from '../common/CONST';

@Component({
  selector: 'app-initial',
  templateUrl: './initial.component.html',
  styleUrls: ['../../../assets/tma/scss/style.scss']
})
export class InitialComponent implements OnInit {

  public otp: string;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private auth: AuthService,
    private cripto: CriptoService,
    private workflow: WorkflowService,
    private guard: SendInformationService<any>,
    private obser: SendInformationService<string>,
    private translate: TranslateService
  ) {
    this.otp = this.route.snapshot.paramMap.get('otp');
    this.translate.setDefaultLang('es-CR');
  }

  ngOnInit() {
    this.auth.deleteAllTokenData();
    this.auth.authenticate(this.otp).subscribe(resauth => {
      if (resauth.state === STATE_ERROR) {
        this.router.navigate(['/forbidden']);
        return;
      } else {
        const tokenDecoded = this.auth.decodeToken(resauth.access_token);
        // {
        //   "iss": "Online JWT Builder",
        //   "iat": 1565906011,
        //   "exp": 1597442011,
        //   "aud": "www.example.com",
        //   "sub": "jrocket@example.com",
        //   "cliendtID": "0123456789",
        //   "partner": "ML",
        //   "product": "CAM",
        //   "lenguaje": "es-CR",
        //   "canal": "web",
        //   "pais": "CR",
        //   "modulo": "que???"
        // }
        this.cripto.getKeysRemote().then(mbassKey => {
          this.cripto.keyCreator().then((respFront: any) => {
            console.log(tokenDecoded);
            const productId = tokenDecoded.product;
            this.auth.saveTokenData(resauth, tokenDecoded.sub, this.saveLocalStorage(productId));
            this.translate.use(tokenDecoded.lenguaje);
            console.error('Hay comentarios que se deben revisar en este componente, no borrar hasta solucionar.');
            this.guard.sendData(productId, PRODUCTO);
            this.workflow.workflow(INITIAL,
              {
                llaveEncriptacion: JSON.stringify(respFront),
              }
            );
          });
        });
      }
    });
  }

  saveLocalStorage(productId: string): boolean {
    switch (productId) {
      case 'CAM_CO':
        return true;
      case 'TMA_CO':
        return true;
      case 'BIO_CO':
        return false;
      default:
        return true;
    }
  }
}
