import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { InitialModule } from 'src/app/modules/initial/initial.module';
import { ForbiddenModule } from '../../forbidden/forbidden.module';
import { PostMessagesComponent } from 'src/app/modules/post-messages/post-messages.component';
import { MODULES_CAM } from '../CONST';

const routes: Routes = [
  {
    path: 'solicitudInicial',
    loadChildren: () => InitialModule
  },
  {
    path: 'cam',
    loadChildren: () => import('../../cam/cam.module').then(mod => mod.CamModule),
    data: {
      producto: MODULES_CAM
    }
  },
  {
    path: 'tma',
    loadChildren: () => import('../../tma/tma.module').then(mod => mod.TmaModule),
    data: {
      producto: 'TMA'
    }
  },
  {
    path: 'forbidden',
    loadChildren: () => ForbiddenModule
  },
  {
    path: 'postMessages',
    component: PostMessagesComponent
  },
  {
    path: '**',
    redirectTo: 'forbidden'
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class BaseRoutingModule { }
