import { Component, OnInit } from '@angular/core';


@Component({
  selector: 'app-post-messages',
  templateUrl: './post-messages.component.html',
  styleUrls: ['./post-messages.component.sass']
})
export class PostMessagesComponent implements OnInit {

  response = [];
  title: string;
  constructor() {
    window.addEventListener('message', event => {
      console.log(event.data);
      this.title = `Respuesta al postMessage:::::${event.data.event}`;
      if (event.data.response) {
        Object.keys(event.data.response).map((key) => {
          this.response.push({
            key,
            value: event.data.response[key]
          });
        });
      }
    });
  }

  ngOnInit() {
  }

  appReady() {
    window.parent.postMessage({
      event: 'appReady',
    }, '*');
  }

  setTitle() {
    const title = prompt('Ingrese el titulo');
    if (title) {
      window.parent.postMessage({
        event: 'setTitle',
        title
      }, '*');
    }
  }

  captureFrontDocument() {
    window.parent.postMessage({
      event: 'captureFrontDocument'
    }, '*');
  }

  captureBackdocument() {
    window.parent.postMessage({
      event: 'captureBackDocument'
    }, '*');
  }

  captureFace() {
    window.parent.postMessage({
      event: 'captureFace'
    }, '*');
  }

  appFinish()  {
    window.parent.postMessage({
      event: 'appFinish'
    }, '*');
  }
}
